<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="cart__data">
    <FormInputBase v-model="formData.name" :error="formError.name" title="" placeholder="   " />

    <FormInputBase v-model="formData.address" :error="formError.address" title=" "
      placeholder="  " />

    <FormInputBase v-model="formData.phone" :error="formError.phone" type="tel" title=""
      placeholder="  " />

    <FormInputBase v-model="formData.email" :error="formError.email" type="email" title="Email"
      placeholder="  Email" />

    <FormInputBase textarea v-model="formData.comment" :error="formError.comment" title="  "
      placeholder=" " />
  </div>
</template>

<script>
import FormInputBase from '@/components/base/FormInputBase.vue';

export default {
  props: {
    value: { default: {} },
    formError: { default: {} },
  },
  components: { FormInputBase },
  computed: {
    formData: {
      get() { return this.value; },
      set(value) { this.$emit('input', value); },
    },
  },
};
</script>
